"use strict";

import { Component, ViewChild, OnInit, ViewEncapsulation } from '@angular/core';
import { fadeInAnimation } from "../../../core/route-animation/route.animation";
import { Utilisateur } from "../metier/utilisateur.metier";
import { FormGroup, FormBuilder } from "@angular/forms";
import { BaseComponent } from "../../../core/component/base-component";
import { UtilisateurService } from "../service/utilisateur.service";
import { PageTitleService } from "../../../core/page-title/page-title.service";

@Component({
  selector: 'ms-utilisateur-list',
  templateUrl: './utilisateur.list.component.html',
  styleUrls: ['./utilisateur.list.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]

})
export class UtilisateurListComponent extends BaseComponent implements OnInit {

  private utilisateur: Utilisateur;
  private utilisateurList: Array<Utilisateur> = [];

  constructor(
    private utilisateurService: UtilisateurService,
    private pageTitleService: PageTitleService, ) {
    super();
  }

  ngOnInit() {
    this.pageTitleService.setTitle("LISTE DES UTILISATEURS");
    this.loadUtilisateurList();
  }


  private loadUtilisateurList() {
    this.utilisateurService.getUtilisateurList()
      .subscribe(utilisateurList => {
        this.utilisateurList = utilisateurList;

        for (let utilisateur of this.utilisateurList) {
          if (utilisateur.photo === null) {
            utilisateur.photo = this.DEFAULT_PHOTO;
          }
        }

      }, error => this.showError(error.status, error.json().message)
      )
      ;
  }


}






