import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {PageTitleService} from '../../../core/page-title/page-title.service';
import {fadeInAnimation} from "../../../core/route-animation/route.animation";
import {Utilisateur} from "../metier/utilisateur.metier";
import {UtilisateurService} from "../service/utilisateur.service";
import {BaseComponent} from "../../../core/component/base-component";
import {ActivatedRoute, Params} from "@angular/router";
import {CustomValidators} from "ng2-validation";
import {ProfilService} from "../../profil/service/profil.service";
import {Profil} from "../../profil/metier/profil.metier";

@Component({
  selector: 'ms-utilisateur-edit',
  templateUrl: './utilisateur.edit.component.html',
  styleUrls: ['./utilisateur.edit.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]
})
export class UtilisateurEditComponent extends BaseComponent implements OnInit {
  private utilisateur: Utilisateur;
  public utilisateurForm: FormGroup;
  private realPassword: string;
  private fakePassword: string = '**********';
  private profilList: Array<Profil> = [];


  constructor(private fb: FormBuilder,
              private route: ActivatedRoute,
              private pageTitleService: PageTitleService,
              private utilisateurService: UtilisateurService,
              private profilService: ProfilService) {
    super();
  }

  ngOnInit() {
    this.loadUtilisateur();
    this.pageTitleService.setTitle("MODIFIER UN UTILISATEUR");
    this.utilisateurForm = this.fb.group({
      id: [Validators.required],
      username: ['', Validators.required],
      password: ['', Validators.required],
      confirmePassword: ['', Validators.required],
      nom: ['', Validators.required],
      prenom: ['', Validators.required],
      email: [null, Validators.compose([CustomValidators.email])],
      profil: [null, Validators.required],
      superAdmin: false,

    });
  }

  updateUtilisateur(utilisateurForm: any) {
    this.utilisateur = new Utilisateur(utilisateurForm);

    this.utilisateur.photo = this.photo;
    if (this.utilisateur.photo === this.DEFAULT_PHOTO) {
      this.utilisateur.photo = null;
    }

    if (this.utilisateur.password === this.fakePassword) {
      this.utilisateur.password = this.realPassword;
      this.realPassword = '';
    }
    this.utilisateurService.saveUtilisateur(this.utilisateur)
      .subscribe(() => {
        this.showInfo("Enregistrement effectué avec succès");
        this.router.navigate(['/admin/utilisateur/list']); // Redierction vers list

        if (this.compteService.compte.username === this.utilisateur.username) {
          if (this.utilisateur.photo != null) {
            this.compteService.compte.photo = this.utilisateur.photo;
          } else {
            this.compteService.compte.photo = this.DEFAULT_PHOTO;
            this.compteService.getUserPhoto();
          }
        }

      }, error => this.showError(error.status, error.json().message))
    ;
  }

  loadUtilisateur() {
    this.route.params.switchMap((params: Params) => this.utilisateurService.getUtilisateur(+params['id']))
      .subscribe(utilisateur => {
          this.utilisateur = utilisateur;

          this.photo = utilisateur.photo;
          if (this.photo == null) {
            this.photo = this.DEFAULT_PHOTO;
          }
          this.loadProfilList();

          this.utilisateurForm.patchValue(this.utilisateur);
          this.realPassword = this.utilisateur.password;
          this.utilisateurForm.get('password').setValue(this.fakePassword);
          this.utilisateurForm.get('confirmePassword').setValue(this.fakePassword);
        }, error => this.showError(error.status, error.json().message)
      );
  }

  deleteUtilisateur(utilisateurForm: any) {
    if (!confirm("Etes-vous de vouloir supprimer ces enregistrements !")) {
      return;
    }
    this.utilisateur = new Utilisateur(utilisateurForm);
    this.utilisateurService.deleteUtilisateur(this.utilisateur)
      .subscribe(() => {
        this.showInfo("Suppression effectué avec succès");
        this.router.navigate(['/admin/utilisateur/list']); // Redierction vers list
      }, error => this.showError(error.status, error.json().message))
    ;
  }

  isPasswordAndConfirmeAreMatched() {
    if (this.utilisateurForm.get('password').value == this.utilisateurForm.get('confirmePassword').value) {
      return true;
    } else {
      return false;
    }
  }

  private loadProfilList() {
    this.profilService.getProfilList()
      .subscribe(profilList => {
          this.profilList = profilList;
          this.utilisateur.profil = this.profilList.find(profil => profil.id === this.utilisateur.profil.id);
          this.utilisateurForm.patchValue({profil: this.utilisateur.profil}, {onlySelf: true});
        }, error => this.showError(error.status, error.json().message)
      )
    ;
  }

}






