import {NgModule} from '@angular/core';
import {RouterModule} from '@angular/router';
import {CommonModule} from '@angular/common';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {FileUploadModule} from 'ng2-file-upload/ng2-file-upload';
import {WizardModule} from 'ng2-archwizard';
import {UtilisateurAddComponent} from './utilisateur.add/utilisateur.add.component';
import {UtilisateurRoutes} from './utilisateur.routing';
import {UtilisateurService} from "./service/utilisateur.service";
import {UtilisateurListComponent} from "./utilisateur.list/utilisateur.list.component";
import {UtilisateurEditComponent} from "./utilisateur.edit/utilisateur.edit.component";
import {DataTableModule} from "angular2-datatable";
import {ProfilService} from "../profil/service/profil.service";

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ReactiveFormsModule,
    FileUploadModule,
    DataTableModule,
    WizardModule,
    RouterModule.forChild(UtilisateurRoutes)
  ],
  declarations: [
    UtilisateurAddComponent,
    UtilisateurListComponent,
    UtilisateurEditComponent,
  ],
  providers: [
    UtilisateurService,
    ProfilService
  ],
})

export class UtilisateurModule {
}



