import {Profil} from "../../profil/metier/profil.metier";

export class Utilisateur {
  id: number;
  username: string = '';
  password: string = '';
  nom: string = '';
  prenom: string = '';
  email: string = '';
  superAdmin: boolean = false;
  photo: string = '';
  profil: Profil;

  constructor(values: Object = {}) {
    Object.assign(this, values);
  }
}



