import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {FormBuilder, FormControl, FormGroup, Validators} from '@angular/forms';
import {CustomValidators} from 'ng2-validation';
import {Utilisateur} from "../metier/utilisateur.metier";
import {UtilisateurService} from "../service/utilisateur.service";
import {BaseComponent} from "../../../core/component/base-component";
import {fadeInAnimation} from "../../../core/route-animation/route.animation";
import {PageTitleService} from "../../../core/page-title/page-title.service";
import {Profil} from "../../profil/metier/profil.metier";
import {ProfilService} from "../../profil/service/profil.service";

const password = new FormControl('', Validators.required);
const confirmPassword = new FormControl('', CustomValidators.equalTo(password));

@Component({
  selector: 'ms-utilisateur-add',
  templateUrl: './utilisateur.add.component.html',
  styleUrls: ['./utilisateur.add.component.scss'],
  encapsulation: ViewEncapsulation.None,
  host: {
    "[@fadeInAnimation]": 'true'
  },
  animations: [fadeInAnimation]
})
export class UtilisateurAddComponent extends BaseComponent implements OnInit {

  public utilisateurForm: FormGroup;
  private utilisateur: Utilisateur;
  private profilList: Array<Profil> = [];

  constructor(private fb: FormBuilder,
              private pageTitleService: PageTitleService,
              private utilisateurService: UtilisateurService,
              private profilService: ProfilService) {
    super();
  }

  ngOnInit() {
    this.pageTitleService.setTitle("AJOUTER UN UTILISATEUR");
    this.utilisateurForm = this.fb.group({
      username: ['', Validators.required],
      password: ['', Validators.required],
      confirmePassword: ['', Validators.required],
      nom: ['', Validators.required],
      prenom: ['', Validators.required],
      email: [null, Validators.compose([CustomValidators.email])],
      profil: [null, Validators.required],
      superAdmin: false,
    });
    this.loadProfilList();
  }

  isPasswordAndConfirmeAreMatched() {
    if (this.utilisateurForm.get('password').value == this.utilisateurForm.get('confirmePassword').value) {
      return true;
    } else {
      return false;
    }
  }

  saveUtilisateur(utilisateurForm: any) {
    this.utilisateur = new Utilisateur(utilisateurForm);

    this.utilisateur.photo = this.photo;
    if (this.utilisateur.photo === this.DEFAULT_PHOTO) {
      this.utilisateur.photo = null;
    }

    this.utilisateurService.saveUtilisateur(this.utilisateur)
      .subscribe(() => {
        this.showInfo("Enregistrement effectué avec succès");
        this.router.navigate(["/admin/utilisateur/list"]);
      }, error => this.showError(error.status, error.json().message))
    ;
  }

  private loadProfilList() {
    this.profilService.getProfilList()
      .subscribe(profilList => {
          this.profilList = profilList;
        }, error => this.showError(error.status, error.json().message)
      )
    ;
  }

}






